<template>
  <div id="mobileUserAdd">
    <Modal
      v-model="mobileUserBuild"
      title=""
      @on-cancel="reset"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="sa008" label="">
          <Input v-model.trim="AddData.sa008" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sa009" label="">
          <Input type="password" password v-model.trim="AddData.sa009" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sa019" label="">
          <Input v-model.trim="AddData.sa019" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sa012" label="">
          <Input v-model.trim="AddData.sa012" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sd001" label="" >
          <Cascader :data="codeData" v-model="AddData.sd001" size="default" change-on-select></Cascader>
        </FormItem>
        <FormItem prop="sa015" label="">
          <Input v-model.trim="AddData.sa015" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sa011" label="">
          <RadioGroup v-model="AddData.sa011">
            <Radio label="0"></Radio>
            <Radio label="1"></Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { insertMobileUser } from '@/api/systemManage'
export default {
  name: 'mobileUserAdd',
  data () {
    return {
      /*  */
      AddData: {
        sa008: '',
        sa012: '',
        sa019: '',
        sa009: '',
        sa015: '',
        sa011: '',
        sd001: [],
        unitList: [1]
      },
      AddRule: {
        sa008: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sa012: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur' }
        ],
        sa009: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sa015: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sd001: [
          { required: true, message: '', trigger: 'change', type: 'array' }
        ]
      },
      /*  */
      mobileUserBuild: false
    }
  },
  props: {
    /*  */
    codeData: {}
  },
  methods: {
    /* axios:  */
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          insertMobileUser({
            data: {
              sa008: this.AddData.sa008,
              sa012: this.AddData.sa012,
              sa019: this.AddData.sa019,
              sa009: this.AddData.sa009,
              sa015: this.AddData.sa015,
              sa011: this.AddData.sa011,
              sd001: Number(this.AddData.sd001[this.AddData.sd001.length - 1]),
              unitList: [1]
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$emit('getTableData')
              this.$Notice.success({ title: '' })
              this.mobileUserBuild = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.$Notice.error({ title: '' })
          })
        }
      })
    },
    // 
    reset () {
      this.AddData = {
        sa008: '',
        sa012: '',
        sa019: '',
        sa009: '',
        sa015: '',
        sa011: '',
        sd001: [],
        unitList: [1]
      }
      this.$refs.add.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
