<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="mobileUserRebuild"
      title="">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="sa001" label="ID" style="display: none">
          {{EditData.sa001}}
        </FormItem>
        <FormItem prop="sa008" label="">
          <Input v-model.trim="EditData.sa008" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sa009" label="">
          ********
        </FormItem>
        <FormItem prop="sa019" label="">
          <Input v-model.trim="EditData.sa019" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sa012" label="">
          <Input v-model.trim="EditData.sa012" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sd001" label="">
          <Cascader :data="codeData" v-model="EditData.sd001" size="default" change-on-select></Cascader>
        </FormItem>
        <FormItem prop="sa015" label="">
          <Input v-model.trim="EditData.sa015" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="sa011" label="">
          <RadioGroup v-model="EditData.sa011">
            <Radio label="0"></Radio>
            <Radio label="1"></Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { editMobileUser } from '@/api/systemManage'
export default {
  name: 'mobileUserEdit',
  data () {
    return {
      EditRule: {
        sa008: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sa012: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur' }
        ],
        sd001: [
          { required: true, message: '', trigger: 'change', type: 'array' }
        ],
        sa015: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      /*  */
      mobileUserRebuild: false,
      /*  */
      EditData: {}
    }
  },
  props: {
    /*  */
    codeData: {},
    /*  */
    editDefaultData: {}
  },
  watch: {
    editDefaultData () {
      this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /*  */
    dataHandle () {
      this.EditData.sa019 = this.editDefaultData.sa019
      this.EditData.sa009 = this.editDefaultData.sa009
      this.EditData.sa015 = this.editDefaultData.sa015
      this.EditData.sa001 = this.editDefaultData.sa001
      this.EditData.sa011 = String(this.editDefaultData.sa011)
      this.$set(this.EditData, 'sa008', this.editDefaultData.sa008)
      this.$set(this.EditData, 'sa012', this.editDefaultData.sa012)
      // this.$set(this.EditData, 'sd001', String(this.editDefaultData.sd001))
      let arr = []
      this.getDefaultCode(this.codeData, String(this.editDefaultData.sd001), arr)
    },
    /*  */
    getDefaultCode (data, code, arr) {
      for (let i in data) {
        if (data[i].value === code) {
          arr.push(data[i].value)
          this.EditData.sd001 = JSON.parse(JSON.stringify(arr))
        } else {
          if (data[0].children.length > 0) {
            arr.push(data[i].value)
            this.getDefaultCode(data[i].children, code, arr)
          }
        }
      }
      arr.pop()
    },
    /*  */
    editSubmit () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          editMobileUser({
            data: {
              sa008: this.EditData.sa008,
              sa012: this.EditData.sa012,
              sa019: this.EditData.sa019,
              sa009: this.EditData.sa009,
              sa015: this.EditData.sa015,
              sa001: this.EditData.sa001,
              sa011: this.EditData.sa011,
              sd001: Number(this.EditData.sd001[this.EditData.sd001.length - 1]),
              unitList: [1]
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '' })
              this.$emit('getTableData')
              this.mobileUserRebuild = false
              this.data = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 
    reset () {
      this.EditData = {
        sa008: '',
        sa012: '',
        sa019: '',
        sa009: '',
        sa015: '',
        sa001: '',
        sa011: '',
        sd001: [],
        unitList: [1]
      }
      this.$refs.edit.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
